.sheet {
   background-color: rgba(189, 156, 130, 0.6);
   width: 100%;
   height: 1200px;
   position: relative;
   padding: 10px;
   background-origin: content-box;
   z-index: 5;
   display: flex;
   justify-content: center;
   align-items: center;
}

/* ------ CUSTOM BORDER ------ */
.custom-border {
   width: calc(100% - 42px);
   height: calc(100% - 42px);
   padding: 20px;
   box-shadow: 0 0 0 3px black, /* Черная линия 3px */ 0 0 0 6px #fff2e6,
      /* Бежевая линия 3px + интервал */ 0 0 0 12px black,
      /* Черная линия 6px + интервал */ 0 0 0 15px #fff2e6,
      /* Бежевая линия 3px + интервал */ 0 0 0 18px black; /* Черная линия 3px + интервал */
}

.corner-img {
   position: absolute;
   width: 36px; /* Ширина изображения */
   height: 36px; /* Высота изображения */
}

.top-left {
   top: 5px;
   left: 5px;
}

.top-right {
   top: 5px;
   right: 5px;
   transform: rotate(90deg);
}

.bottom-left {
   bottom: 5px;
   left: 5px;
   transform: rotate(270deg);
}

.bottom-right {
   bottom: 5px;
   right: 5px;
   transform: rotate(180deg);
}

/* -------------------------- */

/* ----- Flag Stop ----- */

.flagstop {
   position: absolute;
   width: 230px;
   aspect-ratio: 3 / 1;
   top: 30px;
   right: 70px;
   text-align: center;
   display: grid;
   grid-template-columns: 1fr 1fr 1fr;
   grid-gap: 4px;

   p {
      font-size: 1.4em;
   }
}

/* ------------------------ */
.namebox {
   background-image: url("../img/background/name-frame.png");
   background-position: center;
   background-repeat: no-repeat;
   background-size: contain;
   position: absolute;
   width: 300px;
   aspect-ratio: 1 / 1;
   top: 50px;
   left: 60px;
   font-size: 15px;
   .namebox_p {
      position: absolute;
      left: 50%;
      top: 80%;
      transform: translateX(-70%);
   }
}

.abilities {
   position: absolute;
   display: grid;
   top: 370px;
   left: 70px;
   font-size: 10px;
   grid-gap: 16px;
   height: 760px;
   grid-template-rows: 1.3fr 2fr 2.1fr 2fr 3fr 3fr 2.5fr;
   width: 250px;
}

.ability {
   background-color: #fff2e6;

   position: relative;
   border: 3px solid #2d210b;
   border-radius: 3px;

   & .flag {
      background-image: url("../img/background/s-flag.png");
      width: 30%;
      aspect-ratio: 1 / 1;
      position: absolute;
      top: -13px;
      left: 0%;

      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 13px 0 0 3px;

      .num {
         font-weight: 700;
         font-size: 2.5em;
      }
   }

   & .level {
      position: absolute;
      top: 55%;
      left: -3%;
      background-image: url("../img/background/xs-flag.png");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      width: 20%;
      aspect-ratio: 1 / 1;

      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 4.5%;

      p {
         font-size: 1.3em;
      }
   }
}

.flag {
   background-image: url("../img/background/m-flag.png");
   background-position: center;
   background-repeat: no-repeat;
   background-size: contain;
   text-wrap: wrap;
   text-align: center;
   width: 100%;
   aspect-ratio: 1 / 1;
}

.name {
   position: absolute;
   top: 0px;
   left: 30%;
   font-size: 1.6em;
   font-weight: 700;
   height: 26px;
   display: flex;
   align-items: end;
}

.safe {
   position: absolute;
   top: 20%;
   left: 30%;
   font-size: 7px;
   font-weight: 700;
   display: flex;
   align-items: center;

   & p {
      font-size: 1.3em;
      padding-right: 10%;
   }
}

.safebox {
   border: 1.5px solid #2d210b;
   height: 10px;
   width: 10px;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 2em;
   color: darkolivegreen;
}

.skills {
   position: absolute;
   top: 25px;
   left: 16%;
   font-size: 0.8em;
}

li.isSafebox::before {
   font-size: 2ch;
   color: green;
   padding-right: 3px;
   content: "\2618";
   display: inline-block;
   transform: translateY(-5%);
   transform: translateX(-13%);
   line-height: 0;
}

.sheet ul {
   margin: 0px;
   margin-top: 0px;
   padding-left: 31px;
   font-size: 13px;
}

.proficiencyname {
   padding-top: 3%;
   font-size: 1.3em;
}

.sheet h3 {
   font-size: 1em;
}

.spelles {
   position: absolute;
   top: 130px;
   right: 150px;
   width: 267px;
   height: 115px;
   display: grid;
   grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
   grid-gap: 10px;
}

.slots {
   width: 29px;
   position: relative;

   & .slot {
      background-image: url("../img/background/slot.png");
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      height: 40px;
      aspect-ratio: 1 / 1;
      position: relative;
   }

   & .level {
      position: absolute;
      top: 5%;
      left: 65%;
      font-size: 9px;
   }

   & .num {
      position: absolute;
      top: 25%;
      left: 25%;
      font-size: 1em;
   }

   & ul {
      list-style: none;
   }
}

.sheet input {
   border-radius: 100%;
}

.sheet span.spellslot {
   border: 1.5px solid #2d210b;
   background-color: #bd9c82;
   border-radius: 50%;
   cursor: pointer;
   height: 15px;
   width: 15px;
   display: block;
}

.sheet input[type="checkbox"] {
   visibility: hidden;
   display: none;
   opacity: 0;
}

.sheet input[type="checkbox"]:checked + span {
   background-color: #ffa800;
}

.sheet label.inactive span {
   border: 1.5px solid #2d210b7b;
}

.slots .label {
   display: grid;
   grid-gap: 2px;
   padding-left: 26%;
   padding-top: 10%;
}

/* health */
.health {
   background-image: url("../img/background/HP.png");
   background-position: center;
   background-repeat: no-repeat;
   background-size: contain;
   position: relative;

   width: 300px;
   height: 350px;
   top: 330px;
   left: 260px;

   .AC {
      position: absolute;
      top: 10px;
      left: 48px;
      h1 {
         position: absolute;
         left: 40px;
         top: 55px;
         color: white;
      }
   }
   .HP-max {
      position: absolute;
      top: 165px;
      left: 212px;
      text-align: center;
      font-weight: 700;
      line-height: 1.1;
   }
   .HP-max-num {
      font-size: 1.3em;
   }

   .HP {
      position: absolute;
      top: 186px;
      left: 130px;
      text-align: center;

      h1 {
         color: white;
         padding-left: 8px;
         font-size: 2.3em;
      }
      p {
         transform: rotate(20deg);
         font-size: 0.9em;
      }
   }
}

/*------*/

.saves {
   background-image: url("../img/background/saves.png");
   border: 1px solid black;
   width: 10%;
   aspect-ratio: 1 / 1;
   background-position: center;
   background-repeat: no-repeat;
   background-size: contain;
   position: absolute;
   bottom: 20%;
   left: 28.5%;
   display: grid;
   padding-top: 1%;
   grid-template-rows: 1fr 2fr 2fr;

   .saves_name {
      font-size: 0.8em;
      grid-row: 1/2;
   }
   .life,
   .death {
      align-items: center;
      justify-content: center;
      height: 100%;
      .label {
         /* align-items: center; */
         justify-content: center;
         display: grid;
         height: 100%;
         grid-template-columns: 1fr 1fr 1fr;
         label {
            justify-self: center;
            align-self: center;
         }
         & span {
            background-color: rgba(1, 1, 1, 0);
            width: 70%;
            height: 70%;
            min-height: 15px;
            min-width: 15px;
            aspect-ratio: 1 / 1;
         }
      }
   }
   .death {
      grid-row: 2/3;
      .label input[type="checkbox"]:checked + span {
         background-color: black;
      }
   }

   & .life {
      grid-row: 3/4;
      & .label input[type="checkbox"]:checked + span {
         background-color: white;
      }

      .label label {
         align-self: start;
      }
   }
}

.textarea-1 {
   position: absolute;
   bottom: 54px;
   left: 169px;

   & textarea {
      width: 100px;
      height: 150px;
      padding: 12px 10px 10px 20px;
      font-size: 8px;
      resize: none;
   }
}

.sheet textarea {
   background: none;
   border: none;
   font-family: "Luminari";
   color: #2d210b;
}

@media (max-width: 1000px) {
   .sheet {
      height: 10000px;
   }
   .flagstop {
      right: 50%;
      transform: translateX(
         50%
      ); /* Для центрирования относительно правого края */
   }

   .namebox {
      top: 100px;
      left: 50%;
      transform: translateX(
         -50%
      ); /* Для центрирования относительно левого края */
   }
   .health {
      top: 350px;
      left: 50%;
      transform: translateX(
         -50%
      ); /* Для центрирования относительно левого края */
   }

   .abilities {
      top: 750px;
      left: 50%;
      transform: translateX(
         -50%
      ); /* Для центрирования относительно левого края */
   }
   .spelles {
      position: absolute;
      top: 1550px;
      left: 50%;
      transform: translateX(-50%);
      width: 267px;
      height: 115px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr;
      grid-row-gap: 20px;
   }
}
